import createjs from 'create-js-builder';

import plugins from './plugins';

const tween = {
  Ease: createjs.Ease,
  Timeline: createjs.Timeline,
  Tween: createjs.Tween,
  buildDate: new Date(createjs.TweenJS.buildDate),
  version: createjs.TweenJS.version,
}.inherit(plugins);

export default tween;




